const place = ({ icon, name, type, vicinity }) => ({
  icon,
  name,
  type,
  vicinity,
  url: `https://en.wikipedia.org/w/index.php?search=${encodeURI(name)}`
})

const places = placesList =>
  placesList.map((item) => place(item))

export default places



