import React, { Component } from 'react'
import geo from '../helper/geo'
import maps from '../helper/maps'
import wikipedia from '../helper/wikipedia'
import places from '../helper/places'

export default class Location extends Component {

  constructor(props) {
    super(props)
    this.state = {
      places: []
    }
  }

  async addExtract(place) {
    const page =  await wikipedia(place.name)
    if (page) {
      const { extract } = page
      return {
        ...place,
        extract,
      }
    }
    return {
      ...place
    }
  }

  async componentDidMount() {
    const { latitude, longitude } = await geo()
    const results = await maps(latitude, longitude)
    const placesResult = places(results)
    console.log({placesResult})
    const placesWithExtract = []
    for (let idx in placesResult) {
      console.log(idx)
      const placeWithExtract = await this.addExtract(placesResult[idx])
      placesWithExtract.push(placeWithExtract)
    }
    console.log({placesWithExtract})
    this.setState({
      places: placesWithExtract
    })
  }

  render() {
    const { places } = this.state
    if (places.length === 0) {
      return (
        <div>
          <span role="img" aria-label="earth">🌍</span>
          Retrieving location…
        </div>
      )
    }
    return (
      <ul className="places">
        {places.map(({ name, extract, url, icon }) =>
          <li key={url} className="place">
            <img src={icon} alt="icon for type of place" />
            <a href={url}>{name}</a>
            <p>{extract}</p>
          </li>
        )}
      </ul>
    )
  }

}



